import { LitElement, html, css } from 'lit';
import { customElement, property } from 'lit/decorators.js';

@customElement('custom-post')
export class CustomPost extends LitElement {
  @property({ type: String })
  title = '';

  @property({ type: String })
  body = '';

  render() {
    return html`
      <div>
        <h2>${this.title}</h2>
        <p>${this.body}</p>
      </div>
    `;
  }
}
